import React, { Component } from "react";
import { Layout } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class MoaraCuNoroc extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Moara cu noroc</h1>
              <h4>Ioan Slavici</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section">
                <span className="title">Definiția nuvelei psihologice:</span>
                <p>
                  Specie a genului epic,in proza cu dimensiuni mai mari in
                  comparatie cu cele ale schitei si povestirii,dar mai reduse in
                  comparatie cu cele ale romanului, avand un singur fi
                  epic,conflict puternic iar personajele sunt puternic
                  reliefate.Nuvela este psihologica prin tematica,modalitati de
                  caracterizare a personajelor,investigare psihologica si natura
                  conglictului si anume interior.
                </p>

                <div class="section">
                  <span className="title">Trăsături:</span>
                  <p>
                    Opera lui Slavici este ca specie o nuvela psihologica
                    deoarece conflictul major este cel interior (Lupta lui Ghita
                    intre ratiune si patima pentru bani). Prezenta personajelor
                    puternic reliefate (Ghita ,Lica ,Ana),nararea se face la
                    persoana a treia.Este urmarit un singur fir
                    narativ(imbogatirea lui Ghita si instrainarea lui fata de
                    sine si de familie).Timpul relatat este
                    indelungat(peparcursul unui an-de la sfantul Gheorghe pana
                    in anul urmator la Pasti). Este impartita pe Capitole.
                  </p>
                </div>

                <div class="section">
                  <span className="title">Trăsături:</span>
                  <p>
                    Opera lui Slavici este ca specie o nuvela psihologica
                    deoarece conflictul major este cel interior (Lupta lui Ghita
                    intre ratiune si patima pentru bani). Prezenta personajelor
                    puternic reliefate (Ghita ,Lica ,Ana),nararea se face la
                    persoana a treia.Este urmarit un singur fir
                    narativ(imbogatirea lui Ghita si instrainarea lui fata de
                    sine si de familie).Timpul relatat este
                    indelungat(peparcursul unui an-de la sfantul Gheorghe pana
                    in anul urmator la Pasti). Este impartita pe Capitole.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
