import React, { Component } from "react";
import { Layout, Icon, Menu, Affix } from "antd";
import { Link } from "react-router-dom";
const { SubMenu } = Menu;
const { Sider } = Layout;

export default class RomanaSider extends Component {
  render() {
    return (
      <div style={{ minHeight: "90vh", background: "#fff" }}>
        <Affix offsetTop={10}>
          <Sider
            width={300}
            style={{ background: "#fff" }}
            breakpoint="lg"
            collapsedWidth="0"
          >
            <Menu mode="inline" style={{ height: "100%", borderRight: 0 }}>
              <SubMenu
                key="sub1"
                title={
                  <span>
                    <Icon type="italic" />
                    Genul Liric
                  </span>
                }
              >
                <Menu.Item key="1">
                  <Link to="/romana/genul-liric/plumb">
                    Plumb de George Bacovia
                  </Link>
                </Menu.Item>
                <Menu.Item key="2">
                  <Link to="/romana/genul-liric/eu-nu-strivesc-corola-de-minuni-a-lumii">
                    Eu nu strivesc corola de minuni a lumii
                  </Link>
                </Menu.Item>
                <Menu.Item key="3">
                  <Link to="/romana/genul-liric/flori-de-mucigai">
                    Flori de mucigai
                  </Link>
                </Menu.Item>
                <Menu.Item key="4">
                  <Link to="/romana/genul-liric/riga-crypto-si-lapona-enigel">
                    Riga Crypto si Lapona enigel
                  </Link>
                </Menu.Item>
                <Menu.Item key="5">
                  <Link to="/romana/genul-liric/aci-sosi-pe-vremuri">
                    Aci sosi pe vremuri
                  </Link>
                </Menu.Item>
                <Menu.Item key="6">
                  <Link to="/romana/genul-liric/leoaica-tanara-iubirea">
                    Leoaica tanra iubirea
                  </Link>
                </Menu.Item>
              </SubMenu>

              <SubMenu
                key="sub2"
                title={
                  <span>
                    <Icon type="copy" />
                    Genul Epic
                  </span>
                }
              >
                <Menu.Item key="7">
                  <Link to="/romana/genul-epic/harap-alb">Harap Alb</Link>
                </Menu.Item>

                <Menu.Item key="8">
                  <Link to="/romana/genul-epic/alexandru-lapusneanu">
                    Alexandru Lapusneanu
                  </Link>
                </Menu.Item>
                <Menu.Item key="9">
                  <Link to="/romana/genul-epic/moara-cu-noroc">
                    Moara cu noroc
                  </Link>
                </Menu.Item>
                <Menu.Item key="10">option8</Menu.Item>
              </SubMenu>
              <SubMenu
                key="sub3"
                title={
                  <span>
                    <Icon type="diff" />
                    Genul Dramatic
                  </span>
                }
              >
                <Menu.Item key="9">option9</Menu.Item>
                <Menu.Item key="10">option10</Menu.Item>
                <Menu.Item key="11">option11</Menu.Item>
                <Menu.Item key="12">option12</Menu.Item>
              </SubMenu>
            </Menu>
          </Sider>
        </Affix>
      </div>
    );
  }
}
